/* Vendor Orders (Kanban) */

.orders-board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.orders-column {
  display: flex;
  flex-direction: column;
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  overflow: hidden;
  min-height: 70vh;
}

.orders-column.drag-over {
  outline: 2px dashed var(--primary);
  outline-offset: -2px;
}

.orders-column-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 14px;
  background: var(--bg-hover);
  border-bottom: 1px solid var(--divider);
  position: sticky;
  top: 0;
  z-index: 1;
}

.orders-column-title {
  font-size: 14px;
  font-weight: 700;
  color: var(--text-primary);
}

.orders-count {
  background: color-mix(in srgb, var(--primary) 12%, transparent);
  color: var(--primary);
  font-weight: 700;
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 999px;
}

.orders-column-body {
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow-y: auto;
}

.order-card {
  background: var(--bg-main);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 12px;
  cursor: grab;
  user-select: none;
  transition: transform 120ms ease, box-shadow 120ms ease, background 120ms ease;
}

.order-card:hover {
  background: var(--bg-hover);
}

.order-card.dragging {
  opacity: 0.85;
  transform: scale(0.98);
  box-shadow: 0 10px 24px rgba(0,0,0,0.15), 0 2px 8px rgba(0,0,0,0.08);
}

.order-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 6px;
}

.order-id {
  font-weight: 700;
  color: var(--text-primary);
}

.order-total {
  font-weight: 700;
  color: var(--text-primary);
}

.order-customer {
  color: var(--text-primary);
  margin-bottom: 2px;
}

.order-items { color: var(--text-secondary); font-size: 13px; }
.order-meta { color: var(--text-secondary); font-size: 12px; margin-top: 6px; }

.order-footer { margin-top: 10px; display: flex; gap: 8px; align-items: center; }

.btn-group { display: flex; gap: 8px; }

.btn-small {
  appearance: none;
  border: 1px solid var(--border);
  background: var(--bg-surface);
  color: var(--text-primary);
  padding: 6px 10px;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
}

.btn-small:hover { background: var(--bg-hover); }

.btn-small.primary { 
  color: var(--primary); 
  border-color: var(--primary); 
  background: color-mix(in srgb, var(--primary) 8%, transparent);
}
.btn-small.primary:hover { 
  background: color-mix(in srgb, var(--primary) 15%, transparent); 
}

.btn-small.danger { color: var(--error); border-color: var(--error); }
.btn-small.danger:hover { background: color-mix(in srgb, var(--error) 10%, transparent); }

.status-badge { display: inline-block; font-size: 11px; padding: 4px 10px; border-radius: 999px; font-weight: 700; }
.status-badge.done { background: color-mix(in srgb, var(--primary) 12%, transparent); color: var(--primary); }
.status-badge.cancelled { background: color-mix(in srgb, var(--error) 12%, transparent); color: var(--error); }
